.app-layout {
    background-image: url('/images/background.jpg');
    background-size: cover;
    height: 100vh;
    overflow: hidden; 
    display: -ms-flexbox; 
    display: flex;
}

.sidebar-and-view {
    display: -ms-flexbox;
    display: flex;
    height: 100%;
}

.app-container {
    height: 100%;
    width: 100%; 
}

.app-view-container {
    height: 100%;
}

@media screen and (max-width: 768px) {
    
    .app-layout {
        -ms-flex-flow: row wrap;
            flex-flow: row wrap;
    }

    .app-mobile-navbar {
        height: 50px;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
            align-items: center;
        padding-left: 10px;
    }
    
    .app-container {
        -webkit-transition: -webkit-transform .5s;
        transition: -webkit-transform .5s;
        -o-transition: transform .5s;
        transition: transform .5s;
        transition: transform .5s, -webkit-transform .5s;
    }
}



